$menu-color: #61dafb;
$menu-background-color:#20232a;
@mixin hover-colors($normal,$hover) {
    color: $normal;
    &:hover { color: $hover; }
}
.ant-layout-header{
    background: $menu-background-color;
    color: #fff;
    .Col_logo{
        min-width: 72px;
        .logo{
            min-width: 72px;
            font-size: 24px;
            color:$menu-color;
        }
    }
}
.header{
    width: 100%;
    height: 100%;
    position: relative;
    .ant-row{
        height: 100%;
        .ant-col{
            height: 100%;
        }
    }
    .ant-menu{
        height: 100%;
        background: $menu-background-color;
        line-height: 64px;
        .ant-menu-item,.ant-menu-submenu,.ant-menu-active,.ant-menu-item>a{
            height: 100%;
            font-size: 16px;
            box-sizing:border-box;
            @include hover-colors(#fff, $menu-color);
        }
        .ant-menu-item-selected,.ant-menu-submenu-selected{
            color:$menu-color;
            background: $menu-background-color;
            box-sizing:border-box;
            border-bottom: 3px solid $menu-color;
        }
        .ant-menu-vertical{
            background: #fff;
        }
        .ant-menu-item>a{
            text-decoration: none;
        }
    }

}
.ant-menu-dark, .ant-menu-dark .ant-menu-sub{
    background: $menu-background-color;
}
.ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected{
    background-color: $menu-color;
}
.ant-menu-dark .ant-menu-item-group-title{
    color:$menu-color;
}