.ant-carousel{
    background: #282c34;
    color: #fff;
    @media (min-width: 1280px) {
        .welcome_text{
            padding-top: 20px;
            font-size: 30px;
        }
        .key_words{
            font-size: 60px;
        }
        .slick-slider{
            padding-top: 95px;
            padding-bottom: 85px;
            max-width: 1500px;
            margin-left: auto;
            margin-right: auto;
            position: relative;
        }
    }
    @media (max-width: 599px) and (min-width: 0px){
        .welcome_text{
            font-size: 16px;
            max-width: 12em;
            margin-left: auto;
            margin-right: auto;
        }
        .key_words{
            font-size: 30px;
        }
        .slick-slider{
            padding-top: 60px;
            padding-bottom: 70px;
        }
    }
    @media (min-width: 1340px){
        .banner_box{
            max-width: 1260px;
        }
    }
    @media (min-width: 780px){
        .banner_box{
            width: 90%;
        }
    }

}
.banner_box{
    padding-left: 20px;
    padding-right: 20px;
    margin-left: auto;
    margin-right: auto;
}
.welcome_text{
    padding-top: 15px;
    text-align: center;
    font-size: 24px;
    letter-spacing: 0.01em;
    font-weight: 200;
}
.key_words{
    color: #61dafb;
    text-align: center;
    margin: 0;
    font-size: 45px;
    letter-spacing: 0.01em;
}
.slick-slider{
    padding-top: 45px;
    padding-bottom: 10px;
}